
a {
  text-decoration: none;
  color: $black;
}

li {
  list-style: none;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.display-none {
  display: none;
}

.container {
  margin: 0 auto;
}

svg {
  fill: currentColor;
}
